.light {
  @import '~antd/lib/style/themes/default.less';
  @import '~antd/dist/antd.less'; 
  //@import '~antd/dist/antd.compact.less';
  @link-color: #3C3461;
  @link-hover-color:#3c85fa;

  @avatar-bg: #44679F;
  @avatar-color: #DDF5F7;


  @timeline-color:#44679F;
  @timeline-dot-bg: #DDF5F7; 



  @progress-remaining-color:#44679f4f;

  
  ::-webkit-scrollbar {
    width: 6px;
    background-color: transparent;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #44679F;
    border-radius: 6px;
  }
  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
.lightText{
  background-color: #44679F;
  color: #DDF5F7;
}
.light{
  background-color: #DDF5F7;
  color: #3C3461;
}
.light::before,
.light::after{
  background-color: #DDF5F7;
}
.lightTextOnly{
  color: #DDF5F7;
}
.lightTextColor{
  color: #44679F;
}
}