@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root{
  --ligth:#DDF5F7;
  --dark:#3C3461;
  --border:#44679F
}

*{font-family: 'Roboto', sans-serif;}
.card {
  display: flex;
  position: relative;
  justify-content: center;
  flex-direction: column;
  width: 20%;
  min-width: 420px;
  height: 75vh;
  min-height: 550px;
  border-radius: 5px;
  -webkit-box-shadow: 11px 10px 8px -5px rgba(0,0,0,0.23); 
  box-shadow: 11px 10px 8px -5px rgba(0,0,0,0.23);
 
}
#light::after{
  background: linear-gradient(195deg, #4e6d97, #083358);
  background-size: 400% 400%;
  -webkit-animation: aftereffect 10s ease infinite;
  -moz-animation: aftereffect 10s ease infinite;
  animation: aftereffect 10s ease infinite;
}
#dark::after{
  background: linear-gradient(195deg, #4d6a94, #ffd717);
  background-size: 400% 400%;
  -webkit-animation: aftereffect 10s ease infinite;
  -moz-animation: aftereffect 10s ease infinite;
  animation: aftereffect 10s ease infinite;
}
.card::after{
  content: '';
  position: absolute;
  top: 0;
  left: -2%;
  top: -1.3%;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  border-radius:6px;
  z-index: -1;
}
@-webkit-keyframes aftereffect {
  0%{background-position:72% 0%}
  50%{background-position:29% 100%}
  100%{background-position:72% 0%}
}
@-moz-keyframes aftereffect {
  0%{background-position:72% 0%}
  50%{background-position:29% 100%}
  100%{background-position:72% 0%}
}
@keyframes aftereffect {
  0%{background-position:72% 0%}
  50%{background-position:29% 100%}
  100%{background-position:72% 0%}
}

.cardImg{
  flex: 1.5;
  transition: all;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-image: url("../../img/cardimg.jpg");
  position: relative;
  overflow: hidden;
  left: 0;
  top: 0;
  width: 100%;

  background-repeat: no-repeat;
  background-position: 0 28%;
  background-size: cover; 
}
.cardImg::after{

  content: "";
  position: absolute;
  bottom: -45%;
  right: -25%;
  width: 100%;
  height: 50%;
  /*background-color: red;*/
  transform: rotate(-8deg);
}
.cardImg::before{
  content: "";
  position: absolute;
  width: 100%;
  height: 50%;
  bottom: -45%;
  left: -25%;
  transform: rotate(7deg);
}
.cardInformation{
  flex: 1.05;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 10px;
}
h1{
  font-size: 2.3rem;
  transition: all;
}
.autoText{
  display: flex;
  flex: 0.2;
  flex-direction: column;
  position: relative;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding-top: 0%;
  width: 100%;
 
}
.autolight{
  transition: all 1s;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  font-size: 1rem;
  font-weight: bold;
  color: #001F3F;
}
.autodark{
  transition: all 1s;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  font-size: 1rem;
  font-weight: bold;
  color: #FFD717;
}
.autodark::after{
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  content: '|';
  font-weight: bold;
  font-size: 1.5rem;
  -webkit-animation: text 1.3s linear infinite;
  -moz-animation: text 1.3s linear infinite;
  animation: text 1.3s linear infinite;

}
.autolight::after{
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  content: '|';
  font-weight: bold;
  font-size: 1.5rem;
  color: #001F3F;
  -webkit-animation: text 1.3s linear infinite;
  -moz-animation: text 1.3s linear infinite;
  animation: text 1.3s linear infinite;

}
@keyframes text{
  0%{ opacity: 0;}
  49%{ opacity: 0;}
  50%{ opacity: 1;}
  99%{ opacity: 1;}
  100%{opacity: 0;}
} 
.socialMedia{
  display: flex;
  flex: 0.5;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  margin-top: 10px;
  
}
.icons{
  font-size: calc(1.2em + 0.21vw);
  padding: 2px;
}
.icons:hover{
  color: #FFD717;
  transition: all 0.3s;
}

.links{
  display: flex;
  align-items:  flex-end;
  justify-content:center;
  width: 100%;
  position: absolute;
  overflow: hidden;
  bottom: 0;  
}
.links a{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: calc(1em + 0.2vw);
  height: 60px;
  transition: all 0.8s;
  
}
.links .alight:last-child::before{
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  top:0;
  background: linear-gradient(90deg, var(--border) 0%, var(--ligth) 90%);
}
.links .alight:last-child::after{
  content: '';
  position: absolute;
  background: linear-gradient(to top, var(--ligth) 10%,var(--border) 100%);
  width: 1px;
  height: 100%;
  left: 50%;
}
.links .alight:first-child::before{
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  top:0;
  background: linear-gradient(to right, var(--ligth) 0,var(--border) 90%);
}
.links .alight{
  color: #001F3F;
  
}
.links .adark:last-child::before{
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  top:0;
  background: linear-gradient(90deg, var(--border) 0%, var(--dark) 90%);
}
.links .adark:last-child::after{
  content: '';
  position: absolute;
  background: linear-gradient(to top, var(--dark) 10%,var(--border) 100%);
  width: 1px;
  height: 100%;
  left: 50%;
}
.links .adark:first-child::before{
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  top:0;
  background: linear-gradient(to right, var(--dark) 0,var(--border) 90%);
}

.links .adark:hover{
  color: #FFD717;
}
.links .alight:hover{
  color: #3c85fa
}

#themeSwitch{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 1em;
  top: 1em;
  width: 10%;
}
#teste{
  border-radius: 10px;
  width: 100%;

}

