.contactArea{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.contactArea .zonea,
.contactArea .zoneb{
  flex: 1;
}

.contactArea .zonea ul,
.contactArea .zoneb ul{
  display: flex;
  align-items: center;
  position: relative;
  gap: 15px;
}

.rigth{
  right:  80px;
  font-weight: bolder;

}




.zoneDark{
  background-color: #FFD717 ;
  padding: 8px;
  border-radius: 5px;
  color: #44679F;
  font-weight: bolder;
}
.zone{
  background-color: #44679F ;
  padding: 8px;
  border-radius: 5px;
  color: #fff;
  font-weight: bolder;
}