@import url('https://fonts.googleapis.com/css2?family=Passion+One&display=swap');
:root{
  --ligth:#DDF5F7;
  --dark:#4c4279;
  --border:#44679F
}
.pageMain {
  overflow-y: hidden;
  width: 0;
  height: calc(100vh - 245px);
  min-height: 550px;
  transition: all 0.8s;
  opacity: 0.0;
}
.pageMainopen{
  padding: 1%;
  overflow-y: scroll;
  justify-content: center;
  align-items: flex-start;
  width: 45vw;
  height: calc(100vh - 280px);
  min-height: 525px;
  opacity: 1;
  transition: all 0.8s;
  
}
.contentTitle{
  padding: 0.5vw;
  position: relative;
  overflow: hidden;
  width: 100%;
  transition: all 1s;
}
.contentTitle::after{
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  left: 0;
  bottom: 0;
  
}
.contentTitle::before{
  content: '';
  position: absolute;
  right: 0;
  width: 50%;
  bottom: 0;
  height: 1px;
}
.ctdark::before
{
  background: linear-gradient(58deg, rgba(68,129,159,1) 15%,  rgba(76,66,121,1)95%);
}
.ctdark::after
{
  background: linear-gradient(58deg, rgba(76,66,121,1) 15%, rgba(68,129,159,1) 42%);
}
.ctlight::before{
  background: linear-gradient(58deg, rgba(76,66,121,1) 15%, #DDF5F7 95%);
}
.ctlight::after
{
  background: linear-gradient(58deg, #DDF5F7 15%, rgba(68,129,159,1) 42%);
}
.title{
  font-size: 1.59vw;
}
.tlight b{
  color: #fff;
  background-color: #44679F;
  border-radius: .2vw;
  padding: 5px;
  font-weight: 100;
}
.tdark b {
  padding: 5px;
  color: #FFD717;
}
.aboutArea{
  display: flex;
  width: 100%;
  height: auto;
  margin-top: 1.5vh;
  padding: 1vw;
}
.resume{
  flex: 1;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  gap: 0.5vw;
  text-align: left;
  font-size: 17px;
}

.resume::before{
  content: '';
  position: absolute;
  right: -0.50vw;
  bottom: 0;
  width: 1px;
  height: 12.8vw;
}
.information{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.information ul{
  display: flex;
  width: 87%;
  white-space: nowrap;
  overflow: hidden;
}
.informationlight{
  background-color: #44679F;
  color: #DDF5F7;
  border-radius: 4px;
  padding: 3px;
}
.informationDark{
  background-color: #FFD717;
  color: #4c4279;
  border-radius: 4px;
  padding: 3px;
}
.information ul li:last-child{
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.block,
.blocka{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  position: relative;
  flex: 1;
}
.blocka::before{
  content: '';
  position: absolute;
  width: 1px;
  right: -0.50vw;
  height: 110%;
}
.quoteArea{
  display: flex;
  flex-direction: column;

}
.full{
  position: relative;
  box-sizing: border-box;
  width: 100%;
}
.first{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 0.5vw;

}
.quote{
  display: flex;
  position: relative;
  font-size: 1.2vw;
  width: 100%;
}
.quote::after{
  content: '"';
  right: 0;
  top: 2.7vh;
  height: 100%;
  font-size: 3.5vw;
  position:relative;
  font-family: 'Passion One', cursive;
  
}
.quote::before{
  content: '"';
  font-size: 3.5vw;
  top: -2.7vh;
  left: 0;
  transform: scale(-1, 1);
  font-family: 'Passion One', cursive;
  position:relative;
}
.contrast{
  display: flex;
  flex-direction: column;
}
.contrast h3{
  margin-top: -14px;
  font-size: 0.8vw;
}
/*temas deixar no final*/
.d::before{  
  background: linear-gradient(158deg, rgba(68,129,159,1) 65%,  rgba(76,66,121,1)100%);
}
.l::before{
  background: linear-gradient(58deg, #DDF5F7 10%,  rgba(76,66,121,1)100%);
}



/* No animate NOW*/
.animateIn{
  opacity: 0;
  transform: translateX(-50VW);
  animation-name: in;
	animation-duration: 0.5s;
	animation-delay: 0.0s;
  animation-fill-mode: forwards ;
}
@keyframes in {
  0%{}
  100%{
    opacity: 1;
    transform: translateX(0VW);}
}

.animateOut{
  opacity: 1;
  animation-name: out;
	animation-duration: 0.8s;
	animation-delay: 0.0s;
  animation-fill-mode: forwards ;
}

@keyframes out {
  0%{}
  100%{
    opacity: 0;
    transform: translateX(-50VW);}
}

h2{
  font-size: 25px;
}

@media (min-width: 768px) and (max-width: 1366px) {
  .resume{
    flex: 1;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    gap: 0.5vw;
    text-align: left;
    font-size: 13px;
  }
  h2{
    font-size: 22px;
  }
}
