
:root{
  --primary : #C0D9E5;
  --secondary : #719cd8;
  --tertiary : #6ad7e0;
  --primarydark : #02203b;
  --secondarydark : #083358;
  --tertiarydark : rgb(26, 25, 25);
}
.main{
  width: 100vw;
  height: 100vh;
}
.content{
  display: flex;
  flex-direction: row;
  position: absolute;
  justify-content: center;
  align-items: center;
  z-index: 1;
  width: 100vw;
  height: 100vh;
}                            /* Animção do Fundo */
.lightBack{
  position: absolute;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  background-size: 100% 100%;
  -webkit-animation: gradienteffectLigth 30s ease infinite;
  -moz-animation: gradienteffectLigth 30s ease infinite;
  -o-animation: gradienteffectLigth 30s ease infinite;
  animation: gradienteffectLigth 30s ease infinite;
 
}  
.darkBack{
  position: absolute;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  background-size: 100% 100%;
  -webkit-animation: gradienteffectDark 30s ease infinite;
  -moz-animation: gradienteffectDark 30s ease infinite;
  -o-animation: gradienteffectDark 30s ease infinite;
  animation: gradienteffectDark 30s ease infinite;

}   

/*browser compatibilities*/
/*light THEME*/
@-webkit-keyframes gradienteffectLigth {
  0%{background: var(--primary)}
  25%{background: var(--tertiary)}
  50%{background:var(--secondary)}
  75%{background: var(--tertiary)}
  100%{background:var(--primary)}
}
@-moz-keyframes gradienteffectLigth {
  0%{background: var(--primary)}
  25%{background: var(--tertiary)}
  50%{background:var(--secondary)}
  75%{background: var(--tertiary)}
  100%{background:var(--primary)}
}
@-o-keyframes gradienteffectLigth {
  0%{background: var(--primary)}
  25%{background: var(--tertiary)}
  50%{background:var(--secondary)}
  75%{background: var(--tertiary)}
  100%{background:var(--primary)}
} 
@keyframes gradienteffectLigth {
  0%{background: var(--primary)}
  25%{background: var(--tertiary)}
  50%{background:var(--secondary)}
  75%{background: var(--tertiary)}
  100%{background:var(--primary)}
}
/*DARK THEME*/
@-webkit-keyframes gradienteffectDark {
  0%{background: var(--primarydark)}
  25%{background: var(--tertiarydark)}
  50%{background:var(--secondarydark)}
  75%{background: var(--tertiarydark)}
  100%{background:var(--primarydark)}
}
@-moz-keyframes gradienteffectDark {
  0%{background: var(--primarydark)}
  25%{background: var(--tertiarydark)}
  50%{background:var(--secondarydark)}
  75%{background: var(--tertiarydark)}
  100%{background:var(--primarydark)}
}
@-o-keyframes gradienteffectDark {
  0%{background: var(--primarydark)}
  25%{background: var(--tertiarydark)}
  50%{background:var(--secondarydark)}
  75%{background: var(--tertiarydark)}
  100%{background:var(--primarydark)}
} 
@keyframes gradienteffectDark {
  0%{background: var(--primarydark)}
  25%{background: var(--tertiarydark)}
  50%{background:var(--secondarydark)}
  75%{background: var(--tertiarydark)}
  100%{background:var(--primarydark)}
}
                                      /* FIM -- Animção do Fundo */



@media only screen and (max-device-width: 768px) {
  .content{
    display: inline-block;
    padding-left:6%;
    padding-top: 10%;
    overflow: scroll;
  }
  .main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    overflow: scroll;
  }

}