@media only screen and (max-device-width: 768px) {
.mobileScreen{
  display: flex;
  flex-direction: column;
  width: 94%;
  position: relative;
  margin-top: 2%;
}
.mobileScreen .modulePage{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 110%;
  margin-bottom: 2%;
  padding: 4%;
  border-radius: 8px;
}
.quote{
  display: flex;
  width: 100%;
  font-size: 2em;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.quote::after{
  content: '"';
  right: 0;
  top: 0.8em;
  height: 100%;
  font-size: 2em;
  position:relative;
  font-family: 'Passion One', cursive;
  
}
.quote::before{
  content: '"';
  font-size: 2em;
  top: -0.6em;
  left: 0;
  transform: scale(-1, 1);
  font-family: 'Passion One', cursive;
  position:relative;
}

#resume,
#skills{
  width: 100%;
  font-size: 18px;
}

.contrast h2{
  font-size: 2em;
}
.contrast h3{
  font-size: 1.5em;
}
.avatar{
  font-size: 5em;
}
.title {
  font-size: 2.5em;
}
.aboutArea{
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
.resume{
  font-size: 1.5em;
  text-align: center;
  font-weight: bold;
}
.resume::before{
display: none;
}
.information{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.information ul{
  display: flex;
  width: 100%;
  font-size: 1.5em;
  white-space: nowrap;
  overflow: hidden;
}
.blocka::before{
  display: none;
}
.blocka h2,
.block h2{
  font-size: 2.0em;
}
.blocka #blocktext,
.block #blocktext{
  font-size: 1.5em;
  margin-top: -20px;
}

.textcenter b{
  font-size: 1.5em;
}
.timeItem span{
  font-size: 1.3em;
}
.timeItem strong{
  font-size: 1.5em;

} 

.code .textcenter b{
  font-size: 1.6em;
  padding-left: 0.5em;
  padding-right: 0.5em;
}

.knowledge .textcenter b{
  font-size: 1.6em;
  padding-left: 0.5em;
  padding-right: 0.5em;
}
.listKnow{
  font-size: 1.4em;
}

}