@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pontano+Sans&display=swap');

:root{
  --ligth:#DDF5F7;
  --dark:#001F3F;
  --border:#44679F
}
.menulight{
  background-color: var(--ligth);
}
.menudark,
.menudark::after,
.menudark::before{
  background-color: var(--border);
}

.hamlight > div,
.hamlight > div::before,
.hamlight > div::after{
  background: var(--dark);
}
.hamdark > div,
.hamdark > div::before,
.hamdark > div::after{
  background: var(--ligth);
}

.nav{
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  top: -12px;
  background-color: #44679F;
  margin-top: 15px;
  width: 100%;
  height: 3.3em;
  z-index: 0;
  border-radius: 0px;
  transition: all 0.4s;
}
.navActive{
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  top: -12px;
  margin-top: 15px;
  width:100%;
  height: 3.3em;
  z-index: 0;
  border-radius: 0px;
  transition: all 0.4s;
}
.center{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
}
.navMenu{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 60px;
  position: absolute;
  border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  -webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.24);
  -moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.24);
  box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.24);
  border: 0px solid;
  transition: all 1s;
 /* animation-name: menuAnimateRevert;
	animation-duration: 0.4s;
  animation-fill-mode: forwards ;*/
} 
.navMenuActive{

  display: flex;
  justify-content: center;
  align-items: center;
  top: -4px;
  height: 60px;
  width: 60px;
  left: 44%;
  position: absolute;

  border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  -webkit-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.24);
  -moz-box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.24);
  box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.24);
  border: 0px solid;
  transition: all 1s;
  animation-name: menuAnimate;
	animation-duration: 0.6s;
	animation-delay: 0.0s;
  animation-fill-mode: forwards ;

} 
@keyframes menuAnimate {
	from {
    left: 45%;
	} to {
    left: -50px;

	}
}

.itensMenu{
  display: none;
}
.itensMenuActive{
  
  display: flex;
  opacity: 0;
  position: absolute;
  justify-content: flex-end;
  align-items: center;
  gap: 3.0vh;
  width: 135%;
  flex-direction: column; 
  transition: all 1s;
  animation-name: open;
	animation-duration: 0.6s;
	animation-delay:0.7s;
  animation-fill-mode: forwards ;
}

@keyframes open {
  0%{
    opacity: 0;
  }
 25%{
    opacity: 0;
  }
  100%{
    opacity: 1;
    height: 410px;
    
    border-radius: 4px;
    bottom: -10px;
    padding-bottom:  12.164274322169058vh;
    z-index: -1;
  
    filter: drop-shadow(5px 4px 0px  rgba(0, 0, 0, 0.158));
 
  }
  
}
@media (min-width: 600px) and (max-width: 1366px) {
  @keyframes open {
    0%{
      opacity: 0;
    }
   25%{
      opacity: 0;
    }
    100%{
      opacity: 1;
      height: 330px;
      
      border-radius: 4px;
      bottom: -10px;
      padding-bottom:  12.164274322169058vh;
      z-index: -1;
    
      filter: drop-shadow(5px 4px 0px  rgba(0, 0, 0, 0.158));
   
    }
    
  }
}

.itens {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
 

}
.itens a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  transition: all 0.2s;
}
.itens a:hover{
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.5s; 
}
.icon{
  font-size: 25px;
  margin-bottom: 2px;
}

a:hover .icon{
  font-size: 30px;
}
.navText{
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  font-size: 3em;
  font-family: 'Pontano Sans', sans-serif;
  transition: all 0.8s;
}
.navTextActive{
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0;
  padding: .2vw;
  font-size: 4.0vh;
  font-family: 'Pontano Sans', sans-serif;
  transition: all 0.5s;
}
.toggler{
    
    z-index:2;
    height: 50px;
    width: 50px;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
    opacity: 0;
}
.hamburger{
    position: absolute;

    height: 55px;
    width: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    
}
.hamburger > div{
    margin: 10px;
    position: relative;
    top: 0;
    right: 0;
    height: 2px;
    width: 50%;
    transition: all  0.4s ease;
}
.hamburger > div::before,
.hamburger > div::after{
    content: '';
    position: absolute;
    top: -10px;
    width: 100%;
    height: 2px;
    transition: all  0.4s ease;
}
.hamburger > div::after{
    top: 10px;
}
.toggler:checked + .hamburger > div{
    background: rgba(0,0,0,0);
}
.toggler:checked + .hamburger > div::before{
    top: 0;
    transform: rotate(45deg);
   
}
.toggler:checked + .hamburger > div::after{
    top: 0;
    transform: rotate(135deg);
}



@media only screen and (max-device-width: 768px) {

  .nav{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    background-color: #44679F;
    margin-top: 15px;
    width: 100%;
    height: 7.3em;
    z-index: 0;
    border-radius: 0px;
    transition: all 0.4s;
  }
  .navMenu{
    display: none;
  } 
  .navText{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0;
    padding: 1vw;
    font-size: 6em;
    font-family: 'Pontano Sans', sans-serif;
    transition: all 0.5s;
  }
}