.dark {
  @import '~antd/lib/style/themes/default.less';
  @import '~antd/dist/antd.dark.less';
 
  /*
  @import '~antd/dist/antd.dark.less';
  @import '~antd/dist/antd.compact.less'; */
  @link-color: #eaf4f8;
  @link-hover-color:#FFD717;
  @avatar-bg: #FFD717;
  @avatar-color: #4c4279;

  /*
  - tim Line 
  */
  @timeline-color:#DDF5F7;
  @timeline-dot-bg: #4c4279; 
  /*
  - progrees
  */
 
  @processing-color:#FFD717;
  @progress-remaining-color:#eaf4f865;
  @progress-text-color:#FFD717;
  @progress-info-text-color: @progress-text-color;


::-webkit-scrollbar {
    width: 6px;
    background-color: transparent;
}
::-webkit-scrollbar-thumb {
    background-color: #FFD717;
    border-radius: 6px;
}
::-webkit-scrollbar-track {
    background-color: transparent;
}
.darkText{
  background-color: #DDF5F7;
  color: #4c4279;
  transition: all 0.5s;
}
.dark{
  background-color: #4c4279;
  color: #fff;
  transition: all 0.5s;
}
.dark::before,
.dark::after{
  background-color: #4c4279;
  transition: all 0.5s;
}
.darkTextOnly{
  color: #fff;
}
.darkTextColor{
  color: #FFD717;
  font-weight: 400;
}
}