@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,300&display=swap');
.itensTimeLine{
  display: flex;
  justify-content: flex-end;
  margin-right: 10px;
}
.timeLine h2{
  text-align: center;
  margin-bottom: 14px;
}
.textcenter{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 0.3em;

}
strong{
  font-size: larger;
}

.skills{
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.skills div{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.skills .code,.skills .knowledge{
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-around;
  position: relative;
}
.skills .code .space{
  display: flex;
  gap: 15%;
  width: 85%;
}
.skills .code .space .cardCode{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
  height: auto;
  gap: 0.4em;
  text-align: center;
  font-weight: bold;

}
.progress {
  color: #fff;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}
.progress .l{
  color: #000;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}

.skills .code::before{
  content: '';
  position: absolute;
  width: 1px;
  right: -0.50vw;
  background-color: #fff;
  height: 100%;
}

.darkCode b{
  background-color: #FFD717;
  border-radius: .2vw;
  padding: 8px;
  font-weight: 100;
  color:#4c4279;
}
.listKnow{
  padding-left: 10px;
  margin-top: 10px;
  width: 85%;
}

.listKnow ul{
  font-size: clamp(12px,.6em + .45vw,1.3em);
  width: 100%;
  padding: 8px;
  gap: 8px;
}
.listKnow ul li{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  width: 100%;

}
.listKnow ul li .iconsList{
  font-size: 19px;
}
.c{
  color:#FFD717
}
.d::before{  
  background: linear-gradient(158deg, rgba(68,129,159,1) 65%,  rgba(76,66,121,1)100%);
}
.l::before{
  background: linear-gradient(58deg, #DDF5F7 0%,  rgba(76,66,121,1)100%);
}